@font-face {
  font-family: starTrek;
  src: url(../content/TNG_Title.ttf);
}

.map-h2 {
  font-family: starTrek;
  text-transform: uppercase;
  font-size: 1rem;
  padding: 20px;
  padding-left: 10px;
  text-align: center;
}

.google-map {
  width: 100%;
  height: 60vh;
}

.pin {
  display: flex;
  align-items: center;
  width: 180px;
  color: red;
  margin-top: -34px;
  margin-left: -17px;
}

.pin-icon {
  font-size: 4rem;
  margin-top: -34px;
  margin-left: -17px;
}

.pin-text {
  font-size: 1.3em;
}

@media screen and (min-width: 799px) {
  .google-map {
    height: 80vh;
  }

  .map-h2 {
    font-size: 1.3rem;
    font-weight: 400;
  }

  .pin {
    width: 5vw;
  }

  .pin-icon {
    font-size: 10vw;
  }
}